
.card.wait{
  position:absolute;
  left:50%;
  top:50%;
  margin-top: -40px;
  margin-left: -40px;
  width:80px;
  height:80px;
  padding:20px;
  background-color: #000;
  border-radius: 8px;
  opacity: 0.7;
}
.card.wait .preloader-wrapper {
  opacity: 1!important;
}
#ace-query{
  color:#616161;
  font-size: 12px;
  min-height:377px;
}
#ace-query .ace_gutter{
  background-color: #fff;
  border-right:1px #eee solid !important
}
#ace-response{
  overflow:hidden !important;
  min-height:340px;
  background-color: #424242 !important;
}
#ace-response .ace_gutter{
  background-color: #424242 !important;
}
.result .resize{
  cursor: nwse-resize;
  position: absolute;
  bottom:7px;
  right:7px;
  font-size: 13px;
  color:#039be5;
}



