
.root{
  position:relative;
  display: block;
  height:504px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
}
#editor{
  position: absolute;
  top: 35px;
  right: 0;
  bottom: 0;
  left: 0;
  height:470px;
  border:1px solid #eee;
  border-left:0;

  color:#616161;
  font-size: 12px;

}
#editor .ace_gutter{
  background-color: #fafafa;
}

.title{
  border:1px solid #eee;
  background-color: #fafafa;
  height:45px;
  font-size: 13px;
  padding:7px;
  color:#757575
}

.title input{
  display: inline-block;
  margin-top: -6px;
  width:150px;
  margin-bottom: 7px;
}

.title a{
  float:right;
  margin-right:14px;
}
.title a i{
  margin-right:7px;
}



